}

#search form:after {
    font-family: 'FontAwesome';
    -moz-transform: scaleX(-1);
    -webkit-transform: scaleX(-1);
    -ms-transform: scaleX(-1);
    transform: scaleX(-1);
    color: #003399;
    content: '\f002';
    cursor: default;
    display: block;
    position: absolute;
    right: 0;
    text-align: center;
    top: 0px;
    font-size: 18px;
    line-height: 60px;
    width: 60px;
